import messageItem from './message-item/message-item'
import app from '../../App.vue'
const RongIMLib = require('../../lib/RongIMLib.wx-1.2.0.js')
export default {
  components: {
    messageItem
  },
  data() {
    return {
      messageList: [],
      data: {
      	appKey: 'bmdehs6pbguas',
      	token: wx.getStorageSync('token')
      }
    }
  },
  computed: {
  	loginStatus() {
  		return this.$store.state.loginStatus
  	},
    userInfo() {
      return this.$store.state.userInfo
    }
  },
  onLoad() {
    // const self = this
    // uni.$on('newMessageList', function(res) {
    //   console.log(1)
    //   self.getConversationList()
    // })
  },
  onShow() {
    const self = this
    // console.log("登录状态："+ this.loginStatus)

    // if (this.loginStatus === 1) {
    //   this.getConversationList()
    // }
    // console.log(1)
  },
  methods: {

    initSDK(context) {
    	const self = this

    	const appKey = context.data.appKey
    	const token = context.data.token

    	if (appKey == '' || token == '') {
    		// return showPrompt('必须提供 appKey 和 token');
    	}

    	this.$http
    		.imUserRegister({
    			id: self.userInfo.userId,
    			name: self.userInfo.nickName,
    			portraitUri: self.userInfo.avatarUrl
    		})
    		.then(res => {
    			console.log(res)

    			// 公有云初始化
    			const modules = {
    				// protobuf: 'https://cdn.ronghub.com/protobuf-2.3.1.min.js' //支持http(s)网络路径、本地相对路径,
    				RongIMLib: RongIMLib,
    				protobuf: null
    			}

    			const params = {
    				appKey: appKey,
    				token: token,
    				connectToken: res.data.token
    			}

    			const callbacks = {
    				receiveNewMessage: function(message) {
    					uni.$emit('newMessage', message)
    				},
    				getCurrentUser: function(userInfo) {
    				},
    				onTokenIncorrect: function() {
              console.log('token错误重新连接')
    				},
            networkError(msg) {
              app.globalData.Services.Status.reconnect()
            },
    				watchConversationList: function(list) {
              console.log(list)
            }
    			}
    			app.globalData.Services.init(params, callbacks, modules)
    		})
    		.catch(err => {
    			console.log(err)
    		})
    },
    getConversationList() {
      const self = this
      app.globalData.Services.Conversation.getList(function(list) {
        console.log(list)
        self.messageList = list
      })
    }
  }
}
